import React, {Component} from "react";
import axios from 'axios';


export default class Secret extends Component{

    constructor() {
        super();
        this.state = {
          user: {}
        };
      }


    componentDidMount() {
        axios.get('/User/all/'+this.props.email)
          .then(res => {
            this.setState({ user: res.data });
            console.log(this.state.user);
          });
      }


    render (){
        return ( 
            <div>
            <p className="App-intro">
            <div>
            [Secret Area] <br/>
            <label> Usuario registrado: {this.props.email} </label>
            </div>

            if (user) {
                <div>
                <label> Usuario regitrado</label>
                </div>
            }else{
                <div>
                <label> Usuario No registrado</label>
                </div>
             }
          </p>  
            </div>


        
        )


    }




    
}


